<template>
  <div id='main'>
    <navber class="navber" v-if="navbarShow"></navber>
    <div class="content">
      <div class="nav_ber_left" v-if="sideShow">
        <side-column></side-column>
      </div>
      <div class="router_right">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import navber from "./Navbar.vue";
import SideColumn from "../../../public/SideColumn.vue";

export default {
  name: "App",
  components: {
    navber: navber,
    SideColumn: SideColumn
  },
  data () {
    return {
      navbarShow: false,
      sideShow: false,
    };
  },
  created () {
    // if (!localStorage.getItem('userActive')) {
    //   this.$router.push('/login');
    // }
  },
  mounted () {

  },
  methods: {

  },
  watch: {
  }
};
</script>

<style lang="scss" scoped>
#main {
  height: 100%;
  width: 100%;
}
.navber {
  width: 100%;
  position: absolute;
  top: 0;
  // min-width: 1896px;
  /*min-width: 1650px;*/
  z-index: 1000;
}
.content {
  width: 100%;
  position: absolute;
  // top: 61px;
  top: 0;
  bottom: 0px;
  // min-width: 1366px;
  /*min-width: 1650px;*/
  min-height: 888px;
}
.nav_ber_left {
  width: 248px;
  height: 100%;
  float: left;
  transition: all .5s;
  background:rgba(43,51,62,1);
  @media screen and(max-width: 1290px) {
    width: 64px !important;
    transition: all .5s;
  }
}
.router_right {
  // margin-left: 248px;
  width: auto;
  min-width: 1024px;
  height: 100%;
  background: #F5F8FC;
  transition: all .5s;
  // @media screen and(max-width: 1290px) {
  //   margin-left:64px;
  //   transition: all .5s;
  // }
}
</style>

<style>
.tableCom .demo-table-expand {
  font-size: 0;
}
.tableCom .demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.tableCom .demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 80%;
}
.tableCom .el-table__expanded-cell {
  padding-left: 120px !important;
}
.tableCom .demo-table-expand-button {
  width: 100% !important;
}
.tableCom .el-upload-list__item-status-label {
  display: none !important;
}
</style>




