<template>
  <div class="navber">
    <div class="left">
      <!-- <span class="project_name" 
            @click="turnToWorkBench"
            style="cursor:pointer">{{$t('navbar.title')}}</span> -->
      <span class="project_name" 
            @click="turnToWorkBench"
            style="cursor:pointer">Cloud platform</span>
    </div>
    <div class="right">
    <!-- <el-select v-model="language" placeholder="请选择语言" size="mini" class="language" @change="changelanguage">
      <el-option
        label="中文"
        value="zh">
      </el-option>
       <el-option
        label="English"
        value="en">
      </el-option>
    </el-select> -->
      <el-dropdown  @command="handleCommand">
        <span class="el-dropdown-link username_dropdown">
          {{username}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="e" disabled>{{role}}</el-dropdown-item>
          <el-dropdown-item command="logOut" divided>{{$t('navbar.logOut')}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
  name: "navbar",
  data () {
    return {
      username: '',
      language: localStorage.language,
      role: ''
    };
  },
  watch: {
    language() {

    }
  },
  created() {
    if(localStorage.language) {
      // this.$i18n.locale = 'zh'
      this.$i18n.locale = this.language = localStorage.language
    }
  },
  mounted () {
    if(window.location.hash === '#/') {
      this.$nextTick(() => {
        this.$router.push('/workbench/workbench');
      })
    }
    this.username = localStorage.getItem('userActive');
    this.role = localStorage.getItem('role');
    // console.log(this.role)
  },
  methods: {
    // 跳转主页
    turnToWorkBench() {
      this.$router.push('/workbench/workbench')
    },
    changelanguage(lang) {
      // console.log(lang)
      window.location.reload()
      // this.$i18n.locale = lang
      localStorage.language = lang
      this.$EventBus.$emit('initTree')
    },
    handleSelect (key, keyPath) {

    },
    handleCommand (command) {
      switch (command) {
        case 'logOut':
          localStorage.removeItem('userActive');
          localStorage.removeItem('role');
          localStorage.removeItem('token');
          localStorage.removeItem('route');
          //清除路由
          this.$router.options.routes[1].children = [];
          this.$router.addRoutes(this.$router.options.routes);
          this.$nextTick(() => {
            this.$router.push('/login');
            setTimeout(() => {
              window.location.reload();
            }, 100)
          })
          break;
        default:
          break;
      }
    }
  }
};
</script>
<style lang="scss">
  .language {
  width: 120px;
  margin-right: 30px;
  input{
    background:#2B333E;
    color: #fff;
    border: none;
  }
}
</style>
<style lang="scss" scoped>
.navber {
  background: #2B333E;
  display: flex;
  height: 60px;
  justify-content: space-between;
  // box-shadow:0px 0px 18px rgba(230,233,237,1);
  border-bottom: 1px solid #fff;
  color: #fff;
}
.left {
  justify-content: flex-start;
  align-items: center;
  display: flex;
  margin-left: 0.4rem;
}
.project_name {
  align-items: center;
  font-size: 24px;
  margin-left: 10px;
  font-weight: 600;
  /* font-family: Microsoft YaHei; */
}
.right {
  margin-left: 100px;
  align-items: center;
  display: flex;
  margin-right: 80px;
  @media screen and(max-width:1410px){
    margin-right: 50px;
  }
  @media screen and(max-width:1110px){
    margin-right: 40px;
  }
}
.right > span {
  margin-right: 10px;
  color:#fff;
}
.language {
  width: 90px;
  margin-right: 30px;
}
.right label {
  align-items: center;
  color: #d4d6da;
  cursor: pointer;
}

.is-active {
  color: rgb(64, 158, 255) !important;
  border-bottom-color: rgb(64, 158, 255) !important;
  background-color: rgb(50, 64, 87) !important;
}

.username_dropdown {
  color: #ffffff;
  cursor: pointer;
  float: right;
}
</style>



