<script>
import staticImg from '../../../static/json/imgJson.json'
export default {
  name: "sidecolumn",
  data () {
    return {
      childrenRoute: "",
      defaultRoute: "",
      routePath: '',
      currentPath: '',
      isCollapse: false,
      tabImgs: [
        {
          default: staticImg.sideDefaultImages[0],
          active: staticImg.sideActiveImages[0],
        },
        {
          default: staticImg.sideDefaultImages[1],
          active: staticImg.sideActiveImages[1],
        },
        {
          default: staticImg.sideDefaultImages[2],
          active: staticImg.sideActiveImages[2],
        },
        {
          default: staticImg.sideDefaultImages[3],
          active: staticImg.sideActiveImages[3],
        },
        {
          default: staticImg.sideDefaultImages[4],
          active: staticImg.sideActiveImages[4],
        },
      ],
      tabDefaultImg: [
        staticImg.sideDefaultImages[0],
        staticImg.sideDefaultImages[1],
        staticImg.sideDefaultImages[2],
        staticImg.sideDefaultImages[3],
        staticImg.sideDefaultImages[4]
      ],
      tabActiveImg: [
        staticImg.sideActiveImages[0],
        staticImg.sideActiveImages[1],
        staticImg.sideActiveImages[2],
        staticImg.sideActiveImages[3],
        staticImg.sideActiveImages[4],
      ],
      screenWidth: ''
    };
  },
  watch: {
    '$route': 'getPath',
    screenWidth (val) {
      if (!this.timer) {
        console.log(2)
        this.screenWidth = val
        this.timer = true
        let that = this
        setTimeout(function () {
          that.initStyle()
          that.timer = false
        }, 400)
      }
    }
  },
  created() {
    this.currentPath = this.$route.path;
    let currPath = (this.$route.fullPath.split("/"))[1];
    // console.log(currPath)
    // 刷新后，判断当前在哪个路由并置白
    switch(currPath) {
      case 'workbench':
        this.tabDefaultImg[0] = this.tabImgs[0].active;
        break;
      case 'deviceManage':
        this.tabDefaultImg[1] = this.tabImgs[1].active;
        break;
      case 'peopleManage':
        this.tabDefaultImg[2] = this.tabImgs[2].active;
        break;
      case 'dataManage':
        this.tabDefaultImg[3] = this.tabImgs[3].active;
        break;
      case 'systemManage':
        this.tabDefaultImg[4] = this.tabImgs[4].active;
        break;
    }
  },
  mounted () {
    // console.log(window.screenWidth)
    console.log(JSON.stringify(this.$route.path))
    let path = this.$route.matched[0].path;
    let routes;
    //当位置位于/*** 就属于***功能 加载功能的路由渲染列表
    console.log(this.$router)
    switch (path) {
      case "/acs":
        routes = this.$router.options.routes[2].children;
        break;
      default:
        routes = this.$router.options.routes[1].children;
        break;
    }
    // console.log(document.body.clientWidth)
    if(document.body.clientWidth <= 1290) {
      this.isCollapse = true;
    }else {
      this.isCollapse = false;
    }
    this.childrenRoute = routes;
    // 从菜单树中删除不需要显示的模块
    this.childrenRoute.forEach(item => {
      item.children.forEach((i, index) => {
        if(i.meta.show == false) {
          item.children.splice(index, 1)
        }
      })
    })
    // console.log(JSON.stringify(this.childrenRoute))
    this.defaultRoute = this.$route.path;
    const that = this
    window.onresize = () => {
      return (() => {
        console.log(1)
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
        // if(that.screenWidth <= 1290) {
        //   that.isCollapse = true;
        // }else {
        //   that.isCollapse = false;
        // }
      })()
    }
  },
  methods: {
    initStyle() {
      console.log(3)
      if(this.screenWidth <= 1290) {
        this.isCollapse = true;
      }else {
        this.isCollapse = false;
      }
    },
    // 根据path判断是否置白
    getPath() {
      let currPath = (this.$route.path.split("/"))[1];
      let allPath = ['workbench', 'deviceManage', 'peopleManage', 'dataManage', 'systemManage'];
      allPath.forEach((item, index) => {
        if(item == currPath) {
          this.tabDefaultImg[index] = this.tabImgs[index].active
        }else{
          this.tabDefaultImg[index] = this.tabImgs[index].default
        }
      })
    },
    handleOpen (index, indexPath) {
      let num = Number(index)
      // 先全部置灰
      this.tabImgs.forEach((item, i) => {
        this.$set(this.tabDefaultImg, i, item.default)
      })
      // 展开状态的置白
      this.$set(this.tabDefaultImg, num, this.tabActiveImg[num])
    },
    handleClose (index, indexPath) {
      let num = Number(index)
      this.$set(this.tabDefaultImg, num, this.tabImgs[num].default)
    },
    handIndex (path, index) {
      this.$router.push(path);
      this.$refs.side.close(index);
    },
    handleSelect(index, indexPath) {
      // console.log(index)
      // console.log(indexPath)
    },
  },
};
</script>
<style lang='scss'>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 100%;
    min-height: 400px;
  }
.tac{
  .el-menu{
    border: none;
  }
  .el-submenu{
    .el-submenu__title{
      padding-left: 20px !important;
      >img{
        margin-right: 10px;
      }
    }
    .el-menu--inline{
      .el-menu-item-group{
        .el-menu-item-group__title{
          padding: 0;
        }
        .el-menu-item{
          padding-left: 50px !important;
        }
      }
    }
  }
  .is-opened{
    .el-submenu__title{
      color: #fff !important;
      border-left: 3px solid rgba(67,131,220,1);
    }
  }
}
</style>



